<script>
/* eslint-disable */ 
import axios from 'axios'
export default {
  components: {
  },
  data() {
    return {
      univlist: [],
      firstuniv: {},
      seconduniv: {},
      thirduniv: {},
      country: '',
      criteria: [],
      wlevel: '',
      language: [],
      semester: '',
      college: '',
      found: false,
      senddata: {
        country: this.country,
        semester: this.semester,
        college: this.college,
        criteria: this.criteria,
        wlevel: this.wlevel,
        language: this.language
      },
      isLoading: true
    }
  },
  setup() {},
  create() {
    // this.$http.get('/api/univ')
    //   .then((response) => {
    //     this.univlist = response.data
    //     console.log(response.data)
    //     }).catch(err => {
    //       alert(err);
    //       console.log(err);
        // }
  },
  mounted() {
    this.getData()
    this.country = sessionStorage.getItem('country')
    this.criteria = sessionStorage.getItem('criteria')
    this.wlevel = sessionStorage.getItem('wlevel')
    this.language = sessionStorage.getItem('language')
    this.semester = sessionStorage.getItem('semester')
    this.college = sessionStorage.getItem('college')
  },
  unmounted() {},
  methods: {
    async getData() {
      try {
        const res = await axios.get('api/univ')
        this.univlist = res.data
        this.firstuniv = this.univlist[0]
        this.seconduniv = this.univlist[1]
        this.thirduniv = this.univlist[2]
        sessionStorage.setItem('firstuniv', JSON.stringify(this.firstuniv))
        sessionStorage.setItem('seconduniv', JSON.stringify(this.seconduniv))
        sessionStorage.setItem('thirduniv', JSON.stringify(this.thirduniv))
        this.found = true
        setTimeout(this.loading, 3000)
      } catch (err) {
        console.error(err)
      }
    },

    async api(url, method, data) {
      return (
        await axios({
          method: method,
          url: url,
          data: data
        }).catch((e) => {
          console.log(e)
        })
      ).data
    },
    detail1() {
      this.$router.push({ name: 'detailresult', query: { order: 'firstuniv' } })
    },
    detail2() {
      this.$router.push({
        name: 'detailresult',
        query: { order: 'seconduniv' }
      })
    },
    detail3() {
      this.$router.push({ name: 'detailresult', query: { order: 'thirduniv' } })
      },
    loading() {
      this.isLoading = false
    }
  }
}

</script>
