      </div>
    </div>
    <button @click="next" :class="{blackbox: clicked, graybox: !clicked}">선택 완료</button>
  </div>
  
</template>
<script>
export default {
  components: {},
  data() {
    return {
      case: [],
      wlevel: '',
      clicks: [false, false, false, false, false, false, false, false],
      clicked: false
    }
  },
  setup() {},
  create() {},
  mounted() {
    this.case = JSON.parse(sessionStorage.getItem('case'))
  },
  unmounted() {},
  methods: {
    next() {
      if (this.wlevel !== '' && this.clicked === true) {
        if (this.case[4] === true) {
          sessionStorage.setItem('weather', this.wlevel)
          this.$router.push('language')
        } else if (this.case[2] === true) {
          sessionStorage.setItem('weather', this.wlevel)
          this.$router.push('result')
        } else if (this.case[3] === true) {
          sessionStorage.setItem('weather', this.wlevel)
          this.$router.push('result')
        }
      }
    },
    click(event) {
      if (event.target.value === 'hothumid') {
        this.clicks[0] = !this.clicks[0]
        this.clicked = !this.clicked
      } else if (event.target.value === 'warmhumid') {
        this.clicks[1] = !this.clicks[1]
        this.clicked = !this.clicked
      } else if (event.target.value === 'coolhumid') {
        this.clicks[2] = !this.clicks[2]
        this.clicked = !this.clicked
      } else if (event.target.value === 'coldhumid') {
        this.clicks[3] = !this.clicks[3]
        this.clicked = !this.clicked
      } else if (event.target.value === 'hotdry') {
        this.clicks[4] = !this.clicks[4]
        this.clicked = !this.clicked
      } else if (event.target.value === 'warmdry') {
        this.clicks[5] = !this.clicks[5]
        this.clicked = !this.clicked
      } else if (event.target.value === 'cooldry') {
        this.clicks[6] = !this.clicks[6]
        this.clicked = !this.clicked
      } else if (event.target.value === 'colddry') {
        this.clicks[7] = !this.clicks[7]
        this.clicked = !this.clicked
      }
      this.wlevel = event.target.value
    }
  }
}
</script>
